body {
  font-family: Roboto, sans-serif;
  color: #232323;
  background-color: #f9f8fa;
}

#root {
  margin-bottom: 100px;
}

// autocomplete
.autocomplete-dropdown-container {
  position: absolute;
  z-index: 1;
  // border: 1px solid #8080808a;
  // border-radius: 5px;
  padding: 0 5px 5px 0;
}

/* spacing */

.px-md-large {
  padding-left: 4rem;
  padding-right: 4rem;
}
.py-md-medium {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.py-6 {
  padding-top: 5rem;
  padding-bottom: 5rem;
}
.px-6 {
  padding-left: 5rem;
  padding-right: 5rem;
}
.py-medium {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

/* text */
.text-small {
  font-size: 90%;
}
p {
  font-size: 18px;
  line-height: 1.7;
}

.text-accent {
  color: $accent-color;
}
a.text-accent:hover {
  color: darken($accent-color, 5%);
}

/* buttons */

.btn-custom-accent {
  color: #ffffff;
  background-color: $accent-color;
  border-color: $accent-color;
  &:hover {
    color: #ffffff;
    background-color: darken($accent-color, 3%);
    border-color: darken($accent-color, 3%);
  }
}

/* img */
img {
  max-width: 100%;
}

/* Navbar */
.navbar-dark .navbar-nav .nav-link:focus,
.navbar-dark .navbar-nav .nav-link:hover {
  color: rgba(255, 255, 255, 1);
  font-weight: 600;
}

.navbar-dark .navbar-nav .nav-link {
  color: rgba(255, 255, 255, 0.9);
  font-weight: 600;
}
